import request from "../../../node_modules/superagent/superagent";

export const GET_WORK_LIST = 'GET_WORK_LIST';
export const GET_TASK_DETAILS = 'GET_TASK_DETAILS';
export const SELECTED_TASK = 'SELECTED_TASK';

export const GET_CASE_DETAILS = 'GET_CASE_DETAILS';
export const SELECTED_CASE = 'SELECTED_CASE';
export const ACCESS_TOKEN = 'ACCESS_TOKEN';

export const REST_CALL_START = 'REST_CALL_START';
export const REST_CALL_SUCCESS = 'REST_CALL_SUCCESS';
export const REST_CALL_FAIL = 'REST_CALL_FAIL';


// //ENTANDO DEPLOYMENT UNCOMMENT THIS
const GET_TASK_DETAILS_ENDPOINT = "http://" + window.location.host + "/fsi-credit-card-dispute-backoffice/api/kiebpm/taskdetail/";
const COMPLETED_TASK_ENDPOINT = "http://" + window.location.host + "/fsi-credit-card-dispute-backoffice/api/kiebpm/completetask/credit-dispute-case_1.0-SNAPSHOT/";
const GET_CASE_DETAILS_ENDPOINT = "http://" + window.location.host + "/fsi-credit-card-dispute-backoffice/api/kiebpm/credit-dispute-case_1.0-SNAPSHOT/cases/instances/";
const COMPLETED_CASE_ENDPOINT = "http://" + window.location.host + "/fsi-credit-card-dispute-backoffice/api/kiebpm/completecasetask/credit-dispute-case_1.0-SNAPSHOT/";

