export const getTaskDetails = (payload) => {
  return {
    type: GET_TASK_DETAILS,
    payload: payload,
  };
}
export const getCaseDetails = (payload) => {
  return {
    type: GET_CASE_DETAILS,
    payload: payload,
  };
}
export const setAccessToken = (payload) => {
  return {
    type: ACCESS_TOKEN,
    payload: payload,
  };
}
export const selectTask = (payload) => {
  return {
    type: SELECTED_TASK,
    payload: payload,
  };
}
export const selectCase = (payload) => {
  return {
    type: SELECTED_CASE,
    payload: payload,
  };
}
export const restCallSuccess = () => {
  return {
    type: REST_CALL_SUCCESS
  };
};
export const restCallStart = () => {
  return {
    type: REST_CALL_START
  };
};

export const restCallFail = (error) => {
  return {
    type: REST_CALL_FAIL,
    error: error
  };
};

export const remoteGetTaskDetails = (taskId) => {
  return dispatch => {
    dispatch(restCallStart());

    var payloadArray = {};
    request
      .get(GET_TASK_DETAILS_ENDPOINT + taskId)
      .set('Content-Type', 'application/json')
      .then((res) => {
        dispatch(restCallSuccess());
        payloadArray = res.text;
        const resJS = JSON.parse(payloadArray)
        // console.log(JSON.stringify(resJS));
        return dispatch({ type: GET_TASK_DETAILS, payload: resJS });
      })
      .catch((err) => {
        var tmp = {};

        tmp.errorMessage = err.message;
        tmp.errorResponse = err.response;
        dispatch(restCallFail(tmp.errorResponse));
      });
  }
}

export const remoteGetCaseDetails = (accessToken, caseId) => {
  return dispatch => {
      dispatch(restCallStart());

      // console.log("Action " + constant.GET_NOTIFICATION_HISTORY);
      request
          .get(GET_CASE_DETAILS_ENDPOINT + caseId)
          .set('Content-Type', 'application/json')
          .set('Accept', 'application/json')
          .set('Authorization', 'Bearer ' + accessToken)
          .then((res) => {
              dispatch(restCallSuccess());

              const response = res.body;
              console.log("Server Response " + JSON.stringify(response));

              return dispatch(getCaseDetails(response));
          })
          .catch((err) => {
              var tmp = {};
              tmp.errorMessage = err.message;
              tmp.errorResponse = err.response;
              console.log(tmp);
              console.log(err);
              dispatch(restCallFail(tmp.errorResponse));
          });
  }
}
export function remoteApproveTask(taskId) {
  const APPROVE_PAYLOAD =
  {
    "approved_": true
  };
  return dispatch => {
    dispatch(restCallStart());
    var payloadArray = {};
    request
      .post(COMPLETED_TASK_ENDPOINT + taskId)
      .set('Content-Type', 'application/json')
      .set('Accept', 'application/json')
      .send(APPROVE_PAYLOAD)
      .then((res) => {
        dispatch(restCallSuccess());
        payloadArray = res.text;
        console.log(payloadArray);
      })
      .catch((err) => {
        var tmp = {};

        tmp.errorMessage = err.message;
        tmp.errorResponse = err.response;
        dispatch(restCallFail(tmp.errorResponse));
      });
  }
}
