export function remoteApproveCase(accessToken, caseId) {
  const APPROVE_PAYLOAD =
  {
    "approved_": true
  };
  console.log("caseId "+caseId);
  return dispatch => {
    dispatch(restCallStart());
    request
    .post(COMPLETED_CASE_ENDPOINT + caseId)
    .set('Content-Type', 'application/json')
    .set('Accept', 'application/json')
    .set('Authorization', 'Bearer ' + accessToken)
    .send(APPROVE_PAYLOAD)
    .query({ page: 0, pageSize: 1000 })
    .then((res) => {
        dispatch(restCallSuccess());
        const response = res.body;
        console.log("Server Response " + response);
    })
    .catch((err) => {
        var tmp = {};
        tmp.errorMessage = err.message;
        tmp.errorResponse = err.response;
        console.log(tmp);
        console.log(err);
        dispatch(restCallFail(tmp.errorResponse));
    });
  }
}
