import React, { Component } from 'react';
import { connect } from 'react-redux';

import * as action from '../state/actions/actions';

class InfoPage extends Component {

  componentDidMount() {
    const caseId = localStorage.getItem("caseId");
    const accessToken = localStorage.getItem("accessToken");

    // const caseId = "FR-0000000217";
    // const accessToken = "357e35bd34cee4ab83e7b65b8ca22c77";

    if(caseId != null && accessToken != null){
      this.props.setAccessToken(accessToken);
      this.props.selectCase(caseId);
      this.props.remoteGetCaseDetails(accessToken, caseId);
    }
  }

  handleComplete = e => {
    // console.log(this.props.selectedTask);
    const target = e.target;
    const name = target.name;
    const selected = this.props.selectedCase;
    const accessToken = this.props.accessToken;
    const that = this;

    if (name == "approve") {
      Promise.resolve(
        that.props.remoteApproveCase(accessToken, selected)
      )
        .then(function () {
          window.location = 'http://' + window.location.host + '/fsi-credit-card-dispute-backoffice/';
        })

    } else if (name == "reject") {
      Promise.resolve(
        that.props.remoteRejectCase(accessToken, selected)
      )
        .then(function () {
          window.location = 'http://' + window.location.host + '/fsi-credit-card-dispute-backoffice/';
        })
    }
  }

  render() {
    const caseDetails = this.props.caseDetails;




    var content = null;

    if (caseDetails != null) {
      const date = new Date(parseInt(caseDetails['case-started-at']));
      const options = { year: 'numeric', month: 'long', day: 'numeric' };

      content =
        <div className="ibox-content">
          <div className="row">
            <div className="col-md-4">
              <p><strong> Customer | Account Created </strong> <br />
                {date.toLocaleDateString('en-US', options)}
              </p>
            </div>
            <div className="col-md-4" />
            <div className="col-md-4">
              <p><strong>ID</strong><br />
                {caseDetails['case-id']}
              </p>
            </div>
          </div>
          <hr />
          <div className="row">
            <div className="col-md-4">
              <p><strong>Customer Info</strong><br />
                <b> Name </b> <br />
                {caseDetails['case-details'].name} {caseDetails['case-details'].lname}
              </p>
            </div>
            <div className="col-md-4">
              <br />
              <p><b> Phone </b> <br />
              {caseDetails['case-details'].phone}
              </p>
            </div>
            <div className="col-md-4">
              <br />
              <p><b> email </b> <br />
              {caseDetails['case-details'].email}
              </p>
            </div>
          </div>
          <hr />
          <div className="row">
            <div className="col-md-4">
              <p><strong>Company Info</strong><br />
                <b> Country </b> <br />
              {caseDetails['case-details'].country}
              </p>
            </div>
            <div className="col-md-4">
              <br />
              <p><b> Sector  </b> <br />
              {caseDetails['case-details'].jobSector}
              </p>
            </div>
            <div className="col-md-4">
              <br />
              <p><b> sub-industry  </b> <br />
              {caseDetails['case-details'].subIndstry}
               </p>
            </div>
          </div>
          {/* <hr />
          <div className="row">
            <div className="col-md-12">
              <input
                name="approve"
                type="button"
                className="btn btn-primary pull-right"
                onClick={this.handleComplete}
                value="Approve"
                style={{ marginLeft: "10px" }}
              />
              <input
                name="reject"
                type="button"
                className="btn btn-danger pull-right"
                onClick={this.handleComplete}
                value="Reject"
                style={{}}
              />
            </div>
          </div> */}
        </div>;
    }


    return (
      <div className="ibox float-e-margins">
        {content}
      </div>
    );
  }
}

const mapStateToProps = state => ({
  taskDetails: state.taskDetailReducer.taskDetails,
  selectedTask: state.taskDetailReducer.selectedTask,
  caseDetails: state.taskDetailReducer.caseDetails,
  selectedCase: state.taskDetailReducer.selectedCase,
  loading: state.taskDetailReducer.loading,
  error: state.taskDetailReducer.error,
  accessToken: state.taskDetailReducer.accessToken,
});
const mapDispatchToProps = dispatch => ({
  remoteGetTaskDetails: (taskid) => dispatch(action.remoteGetTaskDetails(taskid)),
  selectTask: (taskid) => dispatch(action.selectTask(taskid)),
  selectCase: (caseId) => dispatch(action.selectCase(caseId)),
  remoteGetCaseDetails: (accessToken, caseId) => dispatch(action.remoteGetCaseDetails(accessToken, caseId)),
  remoteApproveTask: (taskid) => dispatch(action.remoteApproveTask(taskid)),
  remoteRejectTask: (taskid) => dispatch(action.remoteRejectTask(taskid)),
  remoteApproveCase: (accessToken, caseId) => dispatch(action.remoteApproveCase(accessToken, caseId)),
  remoteRejectCase: (accessToken, caseId) => dispatch(action.remoteRejectCase(accessToken, caseId)),
  setAccessToken: (accessToken) => dispatch(action.setAccessToken(accessToken)),
});
export default connect(mapStateToProps, mapDispatchToProps)(InfoPage);


